.navbar {
  background-color: #23a3c4;
  height: 50px;
  display: flex;
  justify-content: start;
  align-items: center;
}

.navbar-psychologist-takecare {
  top: 0px;
  position: fixed;
  font-family: "Fredoka One", cursive;
  left: 4%;
  overflow: hidden;
  color: #fff;
  height: 50px;
  display: flex;
  justify-content: start;
  align-items: center;
}

.button-logout-psychologist {
  position: absolute;
  margin-left: 92%;
  /* border-radius: 30px; */
}

.menu-bars {
  position: absolute;
  margin-left: 2rem;
  font-size: 1.5rem;
  background: none;
}

.nav-menu {
  background-color: #77c3d6;
  width: 195px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
  z-index: 100;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}

.nav-text {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}

.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 18px;
  width: 90%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

.nav-text a:hover {
  background-color: rgba(255, 255, 255, 0.4);
}

.nav-menu-items {
  width: 100%;
  padding: 0px;
}

.navbar-toggle {
  background-color: #23a4c4;
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: start;
  align-items: center;
}

span {
  margin-left: 16px;
}
